<template>
        <form v-on:submit.prevent="submitForm">
            <div class="form-group">
                <label for="url">Link</label>
                <input type="text" class="form-control" id="url" placeholder="URL ENLACE" v-model="form.url">
            </div>           
            <b-button class="btn-sm btn-block btn-success" type="submit">DESCARGAR</b-button>
            
        </form>
</template>

<script>
export default {
  data() {
    return {
       form: {
                url: ''
                
            }
    };
  },
  methods:{
    submitForm(){
      this.axios.post('/ytdl',{url: this.form.url},{responseType: 'blob'})
        .then((res) => {
          var filename = "";
          var disposition = res.headers['content-disposition'];
          if (disposition && disposition.indexOf('attachment') !== -1) {
              var filenameRegex = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/;
              var matches = filenameRegex.exec(disposition);
              if (matches != null && matches[1]) { 
                filename = matches[1].replace(/['"]/g, '');
              }
          } 
          const url = window.URL.createObjectURL(new Blob([res.data],{type: 'audio/mp3'}));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', filename);
          document.body.appendChild(link);
          link.click();
            
        })
        .catch((error) => {
            console.log(error)
        }).finally(() => {
            //Perform action in always
        });
    },downloadFile(data) {
        const blob = new Blob([data], );
        const url= window.URL.createObjectURL(blob);
        window.open(url);
    }
  }
};
</script>